"use strict";
Object.defineProperty(exports, "__esModule", { value: true });
const _ = require("lodash");
const Section = require("./section");
const Scene = require("./scene");
const Utils = require("./utils");
class Playback {
    constructor() {
        this.contents = [];
    }
    reset() {
        this.contents = [];
    }
    get() {
        return this.contents.join('\n');
    }
    add(content) {
        // 
        if (this.contents.length == this.max) {
            this.contents.shift();
        }
        this.contents.push(content);
    }
}
class Story {
    static reset() {
        this.playback.reset();
    }
    static formData(data) {
        this.section = new Section.List(Utils.getList(data, 'sections', []));
        this.defaultSectionId = _.get(data, 'startSectionId', this.defaultSectionId);
    }
    static start(sectionId, sceneId) {
        if (sectionId === -1) {
            sectionId = this.defaultSectionId;
        }
        this.section.setCurrentId(sectionId);
        if (sceneId !== -1) {
            this.section.getCurrent().scene.setNextId(sceneId);
        }
        else {
            this.section.getCurrent().reset();
        }
    }
    static getNextScene() {
        let next;
        let section = this.section.getCurrent();
        console.assert(section instanceof Section.Item, 'Model Story: ');
        // 
        do {
            next = section.scene.getNext();
            // 
            if (!next) {
                break;
            }
            if (next.type === Scene.Item.Type.OPERATION) {
                // 
                console.log('Model Story: run ' + next.type + ' ' + next.id);
                let nextId = next.run();
                section.scene.setNextId(nextId);
            }
            else if (next.type === Scene.Item.Type.CONDITION) {
                // 
                console.log('Model Story: run ' + next.type + ' ' + next.id);
                let nextId = next.run();
                section.scene.setNextId(nextId);
            }
            else if (next.type === Scene.Item.Type.JUMP) {
                // 
                console.log('Model Story: run ' + next.type + ' ' + next.id);
                let sectionId = next.sectionId;
                this.section.setCurrentId(sectionId);
                section = this.section.getCurrent();
                console.assert(section instanceof Section.Item, 'Model Story:  ' + sectionId);
                section.reset();
            }
            else {
                // 
                if (next.type === Scene.Item.Type.NORMAL) {
                    // 
                    this.playback.add(next.getPlayback());
                }
                break;
            }
        } while (true);
        return next;
    }
}
Story.defaultSectionId = 1;
Story.defaultSecenId = 1;
Story.playback = new Playback();
exports.default = Story;



