// phaser dispatch 2 listener 
//  listener 
export default class ClickHandle {
    listener = null;
    listenerContext = null;
    isEnable = false;

    reset() {
        this.listener = null;
        this.listenerContext = null;
        this.isEnable = false;
    }

    bind(listener, listenerContext) {
        this.listener = listener;
        this.listenerContext = listenerContext;
        this.isEnable = true;
    }

    onClickHandle() {
        this.isEnable = false;
        this.listener.call(this.listenerContext);
    }
}



